*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    padding: 0;
    margin: 0;
    background: #ffffff;
}

header, main, footer {
    margin: auto;
    max-width: 1920px;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    cursor: pointer;
}

ul {
    list-style: none;
}

@import 'blocks/header/header';
@import 'blocks/header/media';



.container {
    width: 100%;
    margin: auto;
    padding: 0 70px;
}